* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: "Manrope", sans-serif;
  background-color: hsl(218, 23%, 16%);
  display: grid;
  place-items: center;
  min-height: 100vh;
  padding-inline: 1.6rem;
}

.advice-generator {
  width: 100%;
  max-width: 54rem;
  display: grid;
  grid: max-content 3.2rem / 1fr;
  border-radius: 15px;
}

.advice-generator__output {
  background-color: hsl(217, 19%, 24%);
  padding: 4rem 2.4rem 6.4rem;
  display: flex;
  flex-flow: column nowrap;
  gap: 2.4rem;
  justify-content: center;
  align-items: center;
  grid-row: 1/2;
  grid-column: 1/2;
}

.advice-generator__advice-number {
  font-weight: 700;
  font-size: 1.1rem;
  line-height: 1.5rem;
  letter-spacing: 0.4rem;
  color: hsl(150, 100%, 66%);
}
.advice-generator__quote {
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 3.3rem;
  letter-spacing: -0.02rem;
  color: #cee3e9;
  text-align: center;
}
@media screen and (min-width: 545px) {
  .advice-generator__advice-number {
    letter-spacing: 0.5rem;
    font-size: 1.3rem;
    line-height: 1.8rem;
  }

  .advice-generator__quote {
    margin-bottom: 1.6rem;
    font-size: 2.8rem;
    line-height: 3.8rem;
    letter-spacing: -0.03rem;
  }
}

.advice-generator__divider {
  display: flex;
  width: 100%;
}
picture > img {
  margin: auto;
  width: 100%;
}

.advice-generator__btn {
  width: 6.4rem;
  height: 6.4rem;
  background-color: #53ffaa;
  border-radius: 50%;
  grid-row: 2/3;
  grid-column: 1/2;
  place-self: end center;
  transition: box-shadow 250ms;
  display: grid;
  place-items: center;
  cursor: pointer;
  border: none;
  outline: none;
}
.advice-generator__btn:hover {
  box-shadow: 0px 0px 22px #53ffaa;
}
